import React, { Component } from 'react';
import {Form,Row,Col,Button,Card} from 'react-bootstrap';
import cogoToast from 'cogo-toast';
import CustomButton from '../../../components/common/CustomButton';
import Auth from '../../../services/https/Auth';
import ErrorBlock from '../../../components/common/ErrorBlock';
import MySpinner from '../../../components/common/MySpinner';
import UnderlineText from '../../../components/common/UnderlineText';
import app_theme from '../../../app_theme';

export default class ChangePassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
        currentPassword:'',
        newPassword:'',
        confirmPassword:'',
        errorData:null,
        isLoading:false
    };
  }
  onSubmitChangePassword = (e)=>{
    e.preventDefault()
    const{currentPassword,newPassword,confirmPassword} = this.state;
    if(newPassword !== confirmPassword) cogoToast.error("Password do not match")
    this.setState({isLoading:true,errorData:null})
        Auth.changePassword({
            oldPassword:currentPassword,
            newPassword:newPassword
        })
        .then((response)=>{
            this.setState({isLoading:false})
            this.props.history.goBack()
            cogoToast.success("Password changed successfully")
        })
        .catch(err=>{
            console.log("Error Changing Password",err);
            this.setState({errorData:err,isLoading:false})
            cogoToast.error(err.message||"Error Changing Password") 
        })
  }

  renderErrorData = () =>{
    const{errorData} = this.state;
    if(errorData){
     return (
       <ErrorBlock errorData = {errorData}/>
       )
    }else{
      return null
    }
  }

  render() {
    return (
      <div>
          {this.state.isLoading && <MySpinner overlay/>}
          <UnderlineText style={{fontSize: 20,marginBottom: 5}}>CHANGE PASSWORD</UnderlineText>
          <Card  style={{ boxShadow:'none',transform: 'scale(0.9)', padding:50,color:'white',backgroundColor:app_theme.colors.primary.main}} >
                {this.renderErrorData()}
                <Card.Body>
                    <Form onSubmit={this.onSubmitChangePassword}>
                        <Form.Group as={Row} controlId="formHorizontalEmail">
                       
                            <Col sm={6} xs={12}>
                            <Form.Label >
                           CURRENT PASSWORD *
                            </Form.Label>
                                    <Form.Control
                                        required
                                        type="password"
                                        value={this.state.currentPassword}
                                        onChange = {(e)=>this.setState({currentPassword:e.target.value})}
                                        />
                            </Col>
                            <Col></Col>
                        </Form.Group>

                        <Form.Group as={Row} controlId="formHorizontalPassword">
                           
                            <Col sm={6} xs={12}>
                            <Form.Label>
                            NEW PASSWORD *
                            </Form.Label>
                                    <Form.Control
                                        required
                                        type="password"
                                        value={this.state.newPassword}
                                        onChange = {(e)=>this.setState({newPassword:e.target.value})}
                                        />
                            </Col>
                            <Col sm={6} xs={12}>
                            <Form.Label>
                            CONFIRM NEW PASSWORD *
                            </Form.Label>
                                    <Form.Control
                                        required
                                        type="password"
                                        value={this.state.confirmPassword}
                                        onChange = {(e)=>this.setState({confirmPassword:e.target.value})}
                                        />
                                        </Col>
                        </Form.Group>

                        <Form.Group as={Row}>
                            <Col>
                                <div style={{textAlign:'center'}} class="mt-4">
                                        <CustomButton square type="submit">
                                              CHANGE PASSWORD
                                        </CustomButton>
                                    </div>
                            </Col>
                        </Form.Group>
                    </Form>
                </Card.Body>
            </Card>
      </div>
    );
  }
}
