import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { Typography } from '@material-ui/core';
import app_theme from '../../app_theme';

const useStyles = makeStyles((theme) => ({
  root: {
    color:app_theme.colors.text.grey,
    width: '100%',
    maxWidth: 360,
    backgroundColor: app_theme.colors.primary.main,
  },
  icon:{
    color: app_theme.colors.primary.contrastText
  }
}));

export default function SideBarMenu(props) {
  const classes = useStyles();
  const [selectedIndex, setSelectedIndex] = React.useState(1);

  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
  };

  return (
    <div className={classes.root}>
        {/* For Admin sidebar */}
      <List component="nav" aria-label="main mailbox folders"> 
      <ListItem
          button
          selected={selectedIndex === 1}
          onClick={(e) => {
            props.clicked()
            props.history.push('/dashboard')
            handleListItemClick(e, 1)
          }}
        >
          <ListItemText primary="Dashboard" />
        </ListItem>
        <center><Divider style={{ backgroundColor: app_theme.colors.text.grey,width:'90%' }} /></center>
        <ListItem
          button
          selected={selectedIndex === 2}
          onClick={(e) => {
            props.clicked()
            props.history.push('/dashboard/companies')
            handleListItemClick(e, 2)
          }}
        >
          <ListItemText primary="View all companies & sites" />
        </ListItem>
        <center><Divider style={{ backgroundColor: app_theme.colors.text.grey,width:'90%' }} /></center>
        <ListItem
          button
          selected={selectedIndex === 3}
          onClick={(e) => {
            props.clicked()
            props.history.push('/dashboard/monitors')
            handleListItemClick(e, 3)
          }}
        >
          <ListItemText primary="View & schedule monitors" />
        </ListItem>
        <center><Divider style={{ backgroundColor: app_theme.colors.text.grey,width:'90%' }} /></center>
        <ListItem
          button
          selected={selectedIndex === 4}
          onClick={(e) => {
            props.clicked()
            props.history.push('/dashboard/messages')
            handleListItemClick(e, 4)
          }}
        >
          <ListItemText primary="Messages" />
        </ListItem>
        <center><Divider style={{ backgroundColor: app_theme.colors.text.grey,width:'90%' }} /></center>
        <ListItem
          button
          selected={selectedIndex === 5}
          onClick={(e) => {
            props.clicked()
            props.history.push('/dashboard/help')
            handleListItemClick(e, 5)
          }}
        >
          <ListItemText primary="Help" />
        </ListItem>
      </List> 
    </div>
  );
}