import { observable, decorate, action, computed, toJS } from "mobx";
import Auth from "../services/https/Auth";

class UserStore {
  constructor() {
    this.userProfile = {};
    this.currentUser = {};
    this.isLoading = false;
    this.isError = false;
  }

  get getProfile() {
    return toJS(this.userProfile);
  }

  get getCurrentUser() {
    return toJS(this.currentUser);
  }

  get isAdmin() {
    if (this.getCurrentUser?.roles?.includes("ROLE_ADMIN")) {
      return true;
    } else {
      return false;
    }
  }

  get isLoggedIn() {
    if (this.getCurrentUser?.accessToken) {
      return true;
    } else {
      return false;
    }
  }

  setCurrentUserWithToken(userData) {
    this.currentUser = userData || {};
  }

  fetchUserProfile() {
    this.isLoading = true;
    this.isError = false;
    Auth.fetchUserProfile()
      .then((userData) => {
        console.log("user Profile Response", userData);
        this.userProfile = userData;
        this.isLoading = false;
      })
      .catch((err) => {
        this.isError = true;
        this.isLoading = false;
      });
  }

  reset() {
    this.isLoading = false;
    this.isError = false;
    this.userProfile = {};
    this.currentUser = {};
  }
}
decorate(UserStore, {
  userProfile: observable,
  currentUser: observable,
  isLoading: observable,
  isError: observable,
  isLoggedIn: computed,
  isAdmin: computed,
  fetchUserProfile: action,
  setCurrentUserWithToken: action,
  getProfile: computed,
  getCurrentUser: computed,
  reset: action,
});

export default new UserStore();
