import { AppWebRequest } from "../NetworkManager";
import Auth from "../Auth";

class CompanyAndSiteHttpsCommunication {
  constructor() {}
  fetchAllCompany() {
    return AppWebRequest("/company", "get");
  }
  fetchOneCompany(id) {
    return AppWebRequest(`/company/${id}`, "get");
  }
  createCompany(payload) {
    const axiosConfig = {
      data: {
        ...payload,
        clientId: Auth.getUserInfo()?.id,
      },
    };
    return AppWebRequest("/company", "post", axiosConfig);
  }
  updateCompany(id, payload) {
    const axiosConfig = {
      data: payload,
    };
    return AppWebRequest(`/company/${id}`, "put", axiosConfig);
  }
  deleteOneCompany(id) {
    return AppWebRequest(`/company/${id}`, "delete");
  }
}

export default new CompanyAndSiteHttpsCommunication();
