import React, { Component } from "react";
import {
  BootstrapTable,
  TableHeaderColumn,
  InsertButton,
  SearchField,
  ExportCSVButton,
} from "react-bootstrap-table";
import { Breadcrumb, Card } from "react-bootstrap";
import AddIcon from "@material-ui/icons/Add";
import app_theme from "../../../app_theme";
import UnderlineText from "../../../components/common/UnderlineText";
import CustomButton from "../../../components/common/CustomButton";
import { Link } from "react-router-dom";
import { Header, MySpinner } from "../../../components/common";
import { observer } from "mobx-react";
import { MonitorStore } from "../../../stores";
import TryAgainBlock from "../../../components/common/TryAgainBlock";
import { ButtonGroup, Button } from "@material-ui/core";

class Monitors extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  createCustomExportCSVButtonWithOtherOption = (onClick) => {
    return (
      <ButtonGroup
        variant="contained"
        color="primary"
        aria-label="contained primary button group"
        style={{ backgroundColor: app_theme.colors.success.main }}
      >
        <Button
          style={{ backgroundColor: app_theme.colors.success.main }}
          onClick={() => MonitorStore.fetchAllMonitors()}
        >
          Refresh
        </Button>
      </ButtonGroup>
    );
  };

  render() {
    if (MonitorStore.isLoading) {
      return <MySpinner />;
    }

    const options = {
      exportCSVBtn: this.createCustomExportCSVButtonWithOtherOption,
      page: 1, // which page you want to show as default
      sizePerPageList: [
        {
          text: "1",
          value: 1,
        },
        {
          text: "5",
          value: 5,
        },
        {
          text: "All",
          //uncomment
          value: MonitorStore.convertAndGetTableData.length,
          //   value: 5
        },
      ], // you can change the dropdown list for size per page
      sizePerPage: 5, // which size per page you want to locate as default
      pageStartIndex: 1, // where to start counting the pages
      paginationSize: 3, // the pagination bar size.
      prePage: "Prev", // Previous page button text
      nextPage: "Next", // Next page button text
      firstPage: "First", // First page button text
      lastPage: "Last", // Last page button text
      // paginationShowsTotal: this.renderShowsTotal,  // Accept bool or function
      paginationPosition: "bottom", // default is bottom, top and both is all available
      // hideSizePerPage: true > You can hide the dropdown for sizePerPage
      // alwaysShowAllBtns: true // Always show next and previous button
      // withFirstAndLast: false > Hide the going to First and Last page button
    };

    return (
      <div>
        <Header
          containerStyle={{
            backgroundColor: "transparent",
            marginBottom: 20,
            color: "white",
          }}
          noCenter
          headerLeft={
            <UnderlineText customClassName="text-uppercase">
              Monitors List
            </UnderlineText>
          }
          headerRight={
            <CustomButton
              square
              withIcon
              customMUIcon={<AddIcon />}
              width="70%"
              onClick={() =>
                this.props.history.push("/dashboard/monitors/add-schedule")
              }
            >
              SCHEDULE MONITOR
            </CustomButton>
          }
          rightContainerStyle={{ flex: 1 }}
        />

        {MonitorStore.isError ? (
          <TryAgainBlock
            onClickTryAgain={() => MonitorStore.fetchAllMonitors()}
          />
        ) : (
          <BootstrapTable
            version="4"
            data={MonitorStore.convertAndGetTableData}
            options={options}
            striped
            hover
            condensed
            pagination={true}
            search
            exportCSV
            tableStyle={{ backgroundColor: "whitesmoke" }}
            trStyle={{ color: "black" }}
            tableHeaderClass="table-head-rb"
          >
            <TableHeaderColumn width="70" dataSort isKey dataField="sn">
              S.N
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataSort
              dataField="username"
            >
              UserName
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataSort
              dataField="firstName"
            >
              FirstName
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataSort
              dataField="lastName"
            >
              LastName
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="300"
              dataSort
              dataField="email"
            >
              Email
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="mobileNumber"
            >
              Phone
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.town"
              dataFormat={(cell,row)=>row.address?.town}
            >
              Town
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.city"
              dataFormat={(cell,row)=>row.address?.city}
            >
              City
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.post_code"
              dataFormat={(cell,row)=>row.address?.post_code}
            >
              PostCode
            </TableHeaderColumn>
            <TableHeaderColumn
              width="150"
              dataField="action"
              export={false}
              dataFormat={(cell, row) => {
                return (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "space-around",
                      alignItems: "left",
                      flexWrap: "wrap",
                    }}
                  >
                    <Link to={`/dashboard/monitors/add-schedule?monitor_id=${row._id}`}>
                      <CustomButton width="100%">
                        <small>SCHEDULE</small>
                      </CustomButton>
                    </Link>
                  </div>
                );
              }}
            >
              Action
            </TableHeaderColumn>
          </BootstrapTable>
        )}
      </div>
    );
  }
}

export default observer(Monitors);
