
export function ValidateEmail(mail)
{
if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
{
return (true)
}
return (false)
}

export function calcDatePart()
{
    var today = new Date()
    var curHr = today.getHours()
    
    if (curHr < 12) {
      return "Morning"
    } else if (curHr < 18) {
      return "Afternoon"
    } else {
      return "Evening"
    }
}