import { observable, decorate, action, computed, toJS } from "mobx";
import MonitorHttpsCommunication from "../services/https/ServerCommunication/MonitorHttpsCommunication";

class MonitorStore {
  constructor() {
    this.allMonitors = [];
    this.assignedSitesForCurrentMonitor = [];
    this.selectedAssignedSite = {}
    this.isLoading = false;
    this.isError = false;
  }

  get getAllMonitors() {
    return toJS(this.allMonitors);
  }

  get getSelectedAssignedSite() {
    return toJS(this.selectedAssignedSite);
  }

  get getAssignedSitesForCurrentMonitor() {
    return toJS(this.assignedSitesForCurrentMonitor);
  }

  get convertAndGetTableData() {
    return this.getAllMonitors.map((monitor,index)=>{
      monitor.sn = index+1;
      return monitor
    })
  }

  convertAndGetTableDataOfAssignedSites() {
    return this.getAssignedSitesForCurrentMonitor.map((site,index)=>{
      site.sn = index+1;
      return site
    })
  }

  fetchAllMonitors() {
    this.isLoading = true;
    this.isError = false;
    MonitorHttpsCommunication.fetchAllMonitor()
      .then((monitorData) => {
        console.log("monitor Data Response", monitorData);
        this.allMonitors = monitorData;
        this.isLoading = false;
      })
      .catch((err) => {
        this.isError = true;
        this.isLoading = false;
      });
  }

  fetchAllAssignedSitesForCurrentMonitor() {
    this.isLoading = true;
    this.isError = false;
    MonitorHttpsCommunication.getAssignSitesForCurrentMonitor()
      .then((sitesData) => {
        console.log("assigned sitesData Response", sitesData);
        this.assignedSitesForCurrentMonitor = sitesData;
        this.isLoading = false;
      })
      .catch((err) => {
        this.isError = true;
        this.isLoading = false;
      });
  }

  getAssignedSiteById(id){
   return this.getSelectedAssignedSite.find(item=>item._id==id)
  }

  reset() {
    this.isLoading = false;
    this.isError = false;
    this.allMonitors = [];
  }
}
decorate(MonitorStore, {
  allMonitors: observable,
  assignedSitesForCurrentMonitor:observable,
  isLoading: observable,
  isError: observable,
  fetchAllMonitors:action,
  fetchAllAssignedSitesForCurrentMonitor:action,
  convertAndGetTableData:computed,
  getAllMonitors:computed,
  getAssignedSitesForCurrentMonitor:computed,
  reset: action,
});

export default new MonitorStore();
