import React, {Component} from 'react';
import { Button, Modal, Form, Col } from 'react-bootstrap';
import axios from 'axios';
import cogoToast from 'cogo-toast';
import app_theme from "../../app_theme";
import '../../css/forgotPassword.style.css';
import Auth from '../../services/https/Auth';
import ErrorBlock from '../../components/common/ErrorBlock';
import MySpinner from '../../components/common/MySpinner';
import CustomButton from '../../components/common/CustomButton';

export default class ForgotPasswordPopup extends Component{
  constructor(props) {
    super(props);
    this.state = {
        email : '',
        isLoading:false,
        errorData:null
     };
  }

  onSubmit = (e) => {
    const{email  }=this.state;
    e.preventDefault()
    this.setState({isLoading:true,errorData:null})
    Auth.forgotPassword(email)
    .then((response)=>{
        this.setState({isLoading:false})
        cogoToast.success(response.message||"Please check your mail for reset link")
    })
    .catch(err=>{
        console.log("Error Forgot Password",err);
        this.setState({errorData:err,isLoading:false})
        cogoToast.error(err.message||"Error Occurred") 
    })
  }
  renderErrorData = () =>{
    const{errorData} = this.state;
    if(errorData){
     return (
       <ErrorBlock errorData = {errorData}/>
       )
    }else{
      return null
    }
  }

  render(){
    const {email,isLoading } = this.state;
    return (
      <Modal
        {...this.props}
        size="md"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
         {isLoading && <MySpinner overlay/>}
         {this.renderErrorData()}
        <Modal.Header closeButton  style={{
            padding: 20,
            backgroundColor: app_theme.colors.primary.dark,
            color: "white",
          }}>
          <Modal.Title id="contained-modal-title-vcenter">
            Password reset
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{
            padding: 20,
            backgroundColor: app_theme.colors.primary.dark,
            color: "white",
          }}>

         <Form onSubmit={
                    this.onSubmit
            }>
                    <Form.Row>
                        <Form.Group as={Col} controlId="EmailId">
                            <Form.Label>Your registered Email</Form.Label>
                            <Form.Control required value={email} onChange={(e)=>{
                                this.setState({email : e.target.value})
                            }} type="email" placeholder="Enter your registered email"/>
                        </Form.Group>
                    </Form.Row>

               
                    <Form.Row>
                        <Form.Group as={Col} style={{textAlign: 'right'}} controlId="submit">
                            <CustomButton square type="submit">
                              SEND
                            </CustomButton>
                        </Form.Group>
                </Form.Row>
            </Form>

        </Modal.Body>
      </Modal>
    );
  }
}