import React, { Component } from "react";
import {
  BootstrapTable,
  TableHeaderColumn,
  InsertButton,
  SearchField,
  ExportCSVButton,
} from "react-bootstrap-table";
import { Visibility, Edit, Delete } from "@material-ui/icons";
import { Breadcrumb, Card, Dropdown } from "react-bootstrap";
import app_theme from "../../../app_theme";
import { Link } from "react-router-dom";
import UnderlineText from "../../../components/common/UnderlineText";
import { CompanyAndSiteStore } from "../../../stores";
import { observer } from "mobx-react";
import { MySpinner } from "../../../components/common";
import TryAgainBlock from "../../../components/common/TryAgainBlock";
import { ButtonGroup, Button } from "@material-ui/core";

const styles = {
  trCustomStyle: {
    backgroundColor: "black",
  },
};

class Companies extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  createCustomExportCSVButtonWithOtherOption = (onClick) => {
    return (
      <ButtonGroup
        variant="contained"
        color="primary"
        aria-label="contained primary button group"
        style={{ backgroundColor: app_theme.colors.success.main }}
      >
        <Button
          style={{ backgroundColor: app_theme.colors.success.main }}
          onClick={() => CompanyAndSiteStore.fetchAllCompanies()}
        >
          Refresh
        </Button>
      </ButtonGroup>
    );
  };

  render() {
    if (CompanyAndSiteStore.isLoading) {
      return <MySpinner />;
    }

    const options = {
      exportCSVBtn: this.createCustomExportCSVButtonWithOtherOption,
      sizePerPageDropDown:(props)=>{
        return (
              <Dropdown>
                <Dropdown.Toggle variant="light" id="dropdown-basic">
                  {props.currSizePerPage||10}
                </Dropdown.Toggle>
              
                <Dropdown.Menu>
                {
                  [ 10, 25, 50, 100 ].map((n, idx) => {
                    return (
                      <Dropdown.Item key={ idx } onClick={ () => props.changeSizePerPage(n) }>{ n }</Dropdown.Item>
                    );
                  })
                }
                </Dropdown.Menu>
            </Dropdown>
            );
          }
    };

    return (
      <div>
        <UnderlineText customClassName="text-uppercase">
          Companies List
        </UnderlineText>

        {CompanyAndSiteStore.isError ? (
          <TryAgainBlock
            onClickTryAgain={() => CompanyAndSiteStore.fetchAllCompanies()}
          />
        ) : (
          <div style={{marginTop:10}}>

          <BootstrapTable
            version="4"
            data={CompanyAndSiteStore.convertAndGetTableData}
            options={options}
            striped
            hover
            condensed
            pagination={true}
            search
            exportCSV
            tableStyle={{ backgroundColor: "whitesmoke" }}
            trStyle={{ color: "black" }}
            tableHeaderClass="table-head-rb"
          >
            <TableHeaderColumn width="70" dataSort isKey dataField="sn">
              S.N
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="200"
              dataSort
              dataField="name"
            >
              Name
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="300"
              dataSort
              dataField="email"
            >
              Email
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="200"
              dataSort
              dataField="Mobile_phones"
            >
              Mobile Phone
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataSort
              dataField="land_phones"
            >
              Land Phone
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.town"
              dataFormat={(cell,row)=>row.address?.town}
            >
              Town
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.city"
              dataFormat={(cell,row)=>row.address?.city}
            >
              City
            </TableHeaderColumn>
            <TableHeaderColumn
              tdStyle={{ whiteSpace: "normal" }}
              width="150"
              dataField="address.post_code"
              dataFormat={(cell,row)=>row.address?.post_code}
            >
              Post Code
            </TableHeaderColumn>
            
            <TableHeaderColumn
              width="100"
              dataField="action"
              export={false}
              dataFormat={(cell, row) => {
                return (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "space-around",
                      alignItems: "left",
                      flexWrap: "wrap",
                    }}
                  >
                    <Link to={{
                      pathname:"/dashboard/companies/company-viewer",
                      search:`?company_id=${row._id}`
                    }}>
                      VIEW SITES
                    </Link>
                  </div>
                );
              }}
            >
              Action
            </TableHeaderColumn>
          </BootstrapTable>
          </div>
        )}
      </div>
    );
  }
}

export default observer(Companies);
