import React, { Component } from "react";
import { Jumbotron, ProgressBar, Row, Col, Card } from "react-bootstrap";
import { Button, Typography } from "@material-ui/core";
import app_theme from "../../../app_theme";
import CustomCard from "../../../components/common/CustomCard";
import GreetingBlock from "../../../components/common/GreetingBlock";
import { UserStore, CheckListStore, MonitorStore } from "../../../stores";
import { Header, MySpinner } from "../../../components/common";
import UnderlineText from "../../../components/common/UnderlineText";
import { observer } from "mobx-react";
import queryString from "query-string";
import SaveIcon from "@material-ui/icons/Save";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import TryAgainBlock from "../../../components/common/TryAgainBlock";
import SaveSubmitButtonSection from "../../../components/common/SaveSubmitButtonSection";

class CheckList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checklist_id: "",
    };
  }
  componentDidMount() {
    const qs = queryString.parse(this.props.location.search);
    if (qs.checklist_id) {
      this.setState({ checklist_id: qs.checklist_id });
      
    }
  }

  render() {
    const { checklist_id } = this.state;
    if (MonitorStore.isLoading || CheckListStore.isLoading) {
      return <MySpinner />;
    }

    return (
      <div>
        <div className="mt-3">
          <GreetingBlock name={UserStore.getCurrentUser.username} />
          <Header
            containerStyle={{
              backgroundColor: "transparent",
              marginBottom: 20,
              marginTop: 20,
              color: "white",
            }}
            noCenter
            headerLeft={
              <UnderlineText customClassName="text-uppercase">
                THE CHECKLIST
              </UnderlineText>
            }
            headerRight={
              <div style={{ width: "100%" }}>
                <Typography style={{ textAlign: "right" }}>
                  <span style={{ color: "white" }}>CHECKLIST:</span>{" "}
                  <span style={{ color: "white" }}>
                    {CheckListStore.getTotalCompletedPercentage}% completed
                  </span>
                </Typography>
                <ProgressBar
                  variant="success"
                  now={CheckListStore.getTotalCompletedPercentage}
                  style={{ borderRadius: 10, height: 10 }}
                />
                <SaveSubmitButtonSection containerStyle={{textAlign: "right"}}/>
              </div>
            }
            rightContainerStyle={{ flex: 3 }}
          />
        </div>
        <div>
          <Row noGutters>
            {CheckListStore.getCheckList.isCoronaCheckRequired
              ? CheckListStore.getCheckList.checkListData.checkListData.map(
                  (item, index) => {
                    return (
                      <Col xs={12} sm={4}>
                        <CustomCard
                          icon={item.icon}
                          isComplete={item.isComplete}
                          sectionName={`SECTION ${item.id}`}
                          title={item.name}
                          onClick={() => {
                            CheckListStore.setSelectedSectionIndex(index);
                            this.props.history.push(
                              "/dashboard/todo/checklist/checklist-section-viewer"
                            );
                          }}
                          containerStyle={{ marginTop: 5, marginRight: 5 }}
                        />
                      </Col>
                    );
                  }
                )
              : CheckListStore.getCheckList.checkListData.checkListData
                  .filter((item) => item.name.toUpperCase() !== "CORONAVIRUS")
                  .map((item, index) => {
                    return (
                      <Col xs={12} sm={4}>
                        <CustomCard
                          icon={item.icon}
                          isComplete={item.isComplete}
                          sectionName={`SECTION ${item.id}`}
                          title={item.name}
                          onClick={() => {
                            CheckListStore.setSelectedSectionIndex(index);
                            this.props.history.push(
                              "/dashboard/todo/checklist/checklist-section-viewer"
                            );
                          }}
                          containerStyle={{ marginTop: 5, marginRight: 5 }}
                        />
                      </Col>
                    );
                  })}
          </Row>
        </div>
      </div>
    );
  }
}

export default observer(CheckList);
