import React, { Component } from 'react';
import {Card} from '@material-ui/core';
import {Form,Button,Col} from 'react-bootstrap';
import cogoToast from 'cogo-toast';
import { MySpinner, ErrorBlock } from '../../../components/common';
import CustomButton from '../../../components/common/CustomButton';
import UnderlineText from '../../../components/common/UnderlineText';
import MonitorHttpsCommunication from '../../../services/https/ServerCommunication/MonitorHttpsCommunication';
import { MonitorStore } from '../../../stores';

class CreateAccount extends Component {
    constructor(props) {
        super(props);
        this.state={
            username:"",
            firstName:"",
            lastName:"",
            email:"",
            mobileNumber:"",
            isLoading:false,
            errorData:null,
            address:{
                town:'',
                city:'',
                post_code:''
            }
        }
    }

    
    //Signup function
    register = (e) =>{
        e.preventDefault()
        const{username, firstName, lastName, email, mobileNumber,address} = this.state;
        this.setState({isLoading:true, errorData : null})
        var payload = {
            username : username,
            email : email,
            firstName : firstName,
            lastName : lastName,
            mobileNumber : mobileNumber,
            address:address
        }
        MonitorHttpsCommunication.createMonitor(payload)
        .then(response=>{
            console.log(response)
            this.setState({isLoading:false})
            MonitorStore.fetchAllMonitors()
            cogoToast.success(response.message||"Monitor Created Successfully")
            this.props.history.goBack()
        })
        .catch(err=>{
            console.log(err)
            this.setState({isLoading:false,errorData:err})
            cogoToast.error(err.message||"Error Occured!!!")
        })
    }
    
    render() {
        const{username, firstName, lastName, email, mobileNumber,address, isLoading, errorData} = this.state;
        return (
            <div style={{}} >
                    <Card  style={{ boxShadow:'none',transform: 'scale(0.9)', padding:50,color:'white'}} >
                    {isLoading && <MySpinner containerStyle={{}} overlay/>}
                        <UnderlineText>CREATE USER</UnderlineText>
                        <div style={{marginTop:20}}>
                            <Form onSubmit={this.register} >
                                {/* for error data to display */}
                                {errorData?<ErrorBlock errorData={errorData}/>:null}
                                {/* loading spinner */}

                                        <Form.Row> 
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">User Name</Form.Label>
                                                    <Form.Control value={username} onChange={(e)=>{
                                                        this.setState({username:e.target.value})
                                                    }}   type="text" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">First Name</Form.Label>
                                                    <Form.Control value={firstName} onChange={(e)=>{
                                                        this.setState({firstName:e.target.value})
                                                    }}   type="text" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                        </Form.Row> 

                                        <Form.Row> 
                                               
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">Last Name</Form.Label>
                                                    <Form.Control value={lastName} onChange={(e)=>{
                                                        this.setState({lastName:e.target.value})
                                                    }}   type="text" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">Email Address</Form.Label>
                                                    <Form.Control value={email} onChange={(e)=>{
                                                        this.setState({email:e.target.value})
                                                    }}   type="email" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                        </Form.Row> 

                                        <Form.Row> 
                                             
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">Mobile Number</Form.Label>
                                                    <Form.Control value={mobileNumber} onChange={(e)=>{
                                                        this.setState({mobileNumber:e.target.value})
                                                    }}   type="number" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                                <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">Town</Form.Label>
                                                    <Form.Control value={address.town} onChange={(e)=>{
                                                        this.setState({address:{...address,town:e.target.value}})
                                                    }}   type="text" required style={{ border : '1px solid black' }}/>
                                                </Form.Group>
                                        </Form.Row> 

                                        <Form.Row>
                                            <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">City</Form.Label>
                                                    <Form.Control value={address.city} onChange={(e)=>{
                                                        this.setState({address:{...address,city:e.target.value}})
                                                    }}  type="text"  required style={{ border : '1px solid black' }}/>
                                            </Form.Group>
                                            <Form.Group as={Col}>
                                                    <Form.Label className="text-uppercase">Post Code</Form.Label>
                                                    <Form.Control value={address.post_code} onChange={(e)=>{
                                                        this.setState({address:{...address,post_code:e.target.value}})
                                                    }}  type="text"  required style={{ border : '1px solid black' }}/>
                                            </Form.Group>
                                        </Form.Row>
                                    
                                    <div style={{textAlign:'center'}} class="mt-4">
                                        <CustomButton square type="submit">
                                                ADD USER
                                        </CustomButton>
                                    </div>
                                    </Form>
                            </div>
                    </Card>

            </div>
        );
    }
}

export default CreateAccount;