import { AppWebRequest } from "../NetworkManager";
import Auth from "../Auth";
import { MonitorStore } from "../../../stores";

class MonitorHttpsCommunication {
  constructor() {}
  fetchAllMonitor() {
    return AppWebRequest("/get-all-monitors", "get");
  }

  createMonitor(payload) {
    var config = {
      data: payload
    };
    return AppWebRequest("add/monitor", "post", config).then(
      (response) => {
        console.log("signup response", response);
        MonitorStore.fetchAllMonitors()
        return response;
      }
    );
  }

  assignSiteVisitSchedule(payload) {
    var config = {
      data: payload
    };
    return AppWebRequest("/assign-site", "put", config).then(
      (response) => {
        console.log("assign site response", response);
        return response;
      }
    );
  }

  getAssignSitesForCurrentMonitor() {
    return AppWebRequest("/assign-site", "get").then(
      (response) => {
        console.log("assign site response", response);
        return response;
      }
    );
  }

  deleteOneMonitor(id) {
    return AppWebRequest(`/delete/monitor/${id}`, "delete");
  }
}

export default new MonitorHttpsCommunication();
