import React, { Component } from "react";
import {
  Jumbotron,
  ProgressBar,
  Row,
  Col,
  Card,
  Button,
} from "react-bootstrap";
import { Typography, Container } from "@material-ui/core";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import VisibilityIcon from "@material-ui/icons/Visibility";
import PeopleIcon from "@material-ui/icons/People";
import app_theme from "../../../app_theme";
import { observer } from "mobx-react";
import { Header, MySpinner } from "../../../components/common";
import UnderlineText from "../../../components/common/UnderlineText";
import { CheckListStore } from "../../../stores";
import SectionResultItem from "../../../components/common/SectionResultItem";
import CustomButton from "../../../components/common/CustomButton";
import ScoreBoard from "../../../components/page-components/ScoreBoard";
// import CustomCard from './common/CustomCard';
import TryAgainBlock from "../../../components/common/TryAgainBlock";
import SaveSubmitButtonSection from "../../../components/common/SaveSubmitButtonSection";

class CheckListSectionViewer extends Component {
  state={
    anchorEl:null,
    imageCLicked:false,
    text:'',
    selectedImage:null
  }

  renderContent = () => {
    const {open}=this.state;
    if (CheckListStore.selectedStepperIndexInsideSection < 0) {
      if(CheckListStore.getSelectedSection.name.toUpperCase()=="CORONAVIRUS"){
        return (
          <Container maxWidth="sm">
          <h6>To answer the questions, please use the scoring system below:</h6>
          <SectionResultItem
            score={<small>YES</small>}
            text="These are in place"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.success.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />

          <SectionResultItem
            score={<small>NO</small>}
            text="These are not in place "
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.error.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />

          <SectionResultItem
            score={<small>N/A</small>}
            text="Not applicable"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.error.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />
         
      
        </Container>
        )
      }
      return (
        <Container maxWidth="sm">
          <h6>To answer the questions, please use the scoring system below:</h6>
          <SectionResultItem
            score={1}
            text="Not meeting the minimum standards of the Scheme code"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.error.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />
          <SectionResultItem
            score={2}
            text="Meeting compliance standards of the Scheme"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.warning.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />
          <SectionResultItem
            score={3}
            text="Meeting beyond compliance standards of the Scheme"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.warning.light,
            }}
            containerStyle={{ marginBottom: 5 }}
          />
          <SectionResultItem
            score={4}
            text="Demonstrating good practice & above the Scheme standards"
            scoreContainerStyle={{
              backgroundColor: app_theme.colors.success.main,
            }}
            containerStyle={{ marginBottom: 5 }}
          />
        </Container>
      );
    } else if (
      CheckListStore.selectedStepperIndexInsideSection >
      CheckListStore.getSelectedSection.children.length - 1
    ) {
      // CheckListStore.uploadCurrentCheckList()
      CheckListStore.makeSelectedSectionCompleted()
      return (
        <center>
        <Card
     style={{
       backgroundColor:app_theme.colors.success.main,
       display:'flex',
       justifyContent:'center',
       alignItems:'center',
       flex:1,
       width:'70%'
     }}
   >
     <Card.Body>
     <div className="text-center font-weight-bold d-flex justify-content-center align-items-center p-5">
       <div>
         <small
           style={{ fontWeight: "bold", color: "white",textTransform:'uppercase',fontSize:20 }}
         >
           {`SECTION ${CheckListStore.getSelectedSection.id}`}
         </small>
         <h5
           style={{
             fontWeight: "bold",
             color: "white",
             textTransform:'uppercase',
             fontSize:25
           }}
         >
           Complete
         </h5>
       </div>
     </div>
     </Card.Body>
   </Card>
   </center>
      );
    } else {
      const currentContent = CheckListStore.getCurrentStepperContent
      const currentSection = CheckListStore.getSelectedSection
      return (
        <ScoreBoard
          listCount={currentContent.id}
          question={currentContent.question}
        >
          {
            currentContent.options.map((option,index)=>{
              return(
                <ScoreBoard.RowItem
                section={currentSection}
                item={option}
                label={option.name}
                onClickScore={(score) => {
                  CheckListStore.updateOptionValueForName(option.name,[{
                    key:'score',
                    value:score
                  },{
                    key:'isComplete',
                    value:true
                  }])
                }}
                onClickAddComment={(comment) => {
                 document.body.click()
                  CheckListStore.updateOptionValueForName(option.name,[{
                    key:'comment',
                    value:comment
                  }])
                }}
                onClickTakePhoto={(imagePath) => {
                  CheckListStore.updateOptionValueForName(option.name,[{
                    key:'photoPath',
                    value:[...option.photoPath,imagePath]
                  }])
                }}
                score={option.score}
                open={open}          
              />
              )
            })
          }
      
        </ScoreBoard>
      );
    }
  };

  renderFooter = () => {
    if (CheckListStore.selectedStepperIndexInsideSection < 0) {
      return (
        <center>
          <CustomButton
            withIcon
            square
            onClick={() =>CheckListStore.incrementStepper()}
          >
            START
          </CustomButton>
        </center>
      );
    } else if (
      CheckListStore.selectedStepperIndexInsideSection >
      CheckListStore.getSelectedSection.children.length - 1
    ) {
      return (
        <center>
          <div style={{ display: "flex",justifyContent:'center' }}>
            <CustomButton
              square
              buttonStyle={{ marginRight: 5, backgroundColor: "transparent" }}
              onClick={() =>{
                  // CheckListStore.uploadCurrentCheckList();
                  this.props.history.goBack();                
              }}
            >
              BACK TO MAIN MENU
            </CustomButton>
          { !CheckListStore.isLastItem() && <CustomButton
              withIcon
              square
              onClick={() =>{
                // CheckListStore.uploadCurrentCheckList();
                CheckListStore.setNextSection()
              }}
            >
              START SECTION {CheckListStore.getSelectedSection.id + 1}
            </CustomButton>}
          </div>
        </center>
      )
    } else {
      return (
        <center>
          <div style={{ display: "flex",justifyContent:'center' }}>
            <CustomButton
              square
              buttonStyle={{ marginRight: 5, backgroundColor: "transparent" }}
              onClick={() =>CheckListStore.decrementStepper()}
            >
              BACK
            </CustomButton>
            <CustomButton
              disabled={!CheckListStore.isSelectedSectionStepperOptionsFinished()}
              withIcon
              square
              onClick={() =>{
                CheckListStore.makeCurrentStepperContentCompleted()
                CheckListStore.incrementStepper()
              }}
            >
              NEXT
            </CustomButton>
          </div>
        </center>
      );
    }
  }; 

  render() {
    const isContentSection = CheckListStore.selectedStepperIndexInsideSection>=0 && CheckListStore.selectedStepperIndexInsideSection<CheckListStore.getSelectedSection.children.length
    const {anchorEl,imageClicked,text,selectedImage}=this.state;
    if (CheckListStore.isLoading || !CheckListStore.getSelectedSection) {
      return <MySpinner />
    }
    if (CheckListStore.isCheckListUploadError) {
      return (
        <TryAgainBlock 
        message="Error Occurred While Uploading Your Progress. You can again try to upload by clickig on below button!"
onClickTryAgain={()=>CheckListStore.uploadCurrentCheckList()}
        />
      )
    }
    return (
      <div style={{height:'100vh'}}>
          {/* Header */}
        <div style={{height:'10%'}}>
        <div>
          <Header
            containerStyle={{
              backgroundColor: "transparent",
              color: "white",
            }}
            noCenter
            headerLeft={
              <div style={{marginRight:5}}>
              <UnderlineText customClassName="text-uppercase">
                {CheckListStore.getSelectedSection.name}
              </UnderlineText>
             {isContentSection && <div style={{color:app_theme.colors.text.grey,marginTop:5,fontSize:12}}>{CheckListStore.getCurrentStepperContent?.subSectionName}</div>}
              </div>
            }
            headerRight={
              <div style={{ width: "100%" }}>
                <Typography style={{ textAlign: "right" }}>
                  <span style={{ color: "white" }}>
                    SECTION {CheckListStore.getSelectedSection.id}:
                  </span>{" "}
                  <span style={{ color: "white" }}>
                    {CheckListStore.getSelectedSectionTotalCompletedPercentage}%
                    completed
                  </span>
                </Typography>
                <ProgressBar
                  variant="success"
                  now={
                    CheckListStore.getSelectedSectionTotalCompletedPercentage
                  }
                  style={{ borderRadius: 10, height: 10 }}
                />
                <SaveSubmitButtonSection containerStyle={{textAlign: "right"}}/>
              </div>
            }
            rightContainerStyle={{ flex: 3 }}
          />
        </div>
        </div>
         {/* Content */}
        <div style={{height:'70%',marginTop:10,overflowY:'scroll'}} className="disable-scrollbars">
       
          <div>{this.renderContent()}</div>
        </div>
         {/* Footer */}
        <div style={{height:'10%',marginTop:10}}>
        {this.renderFooter()}
        </div>
      </div>
    );
  }
}

export default observer(CheckListSectionViewer);
