import { observable, decorate, action, computed, toJS } from "mobx";
import CompanyAndSiteHttpsCommunication from "../services/https/ServerCommunication/CompanyAndSiteHttpsCommunication";

class CompanyAndSiteStore {
  constructor() {
    this.allCompanies = [];
    this.isLoading = false;
    this.isError = false;
  }

  get getAllCompanies() {
    return toJS(this.allCompanies);
  }

  get convertAndGetTableData() {
    return this.getAllCompanies.map((comp,index)=>{
      comp.sn = index+1;
      return comp
    })
  }

  fetchAllCompanies() {
    this.isLoading = true;
    this.isError = false;
    CompanyAndSiteHttpsCommunication.fetchAllCompany()
      .then((companiesData) => {
        console.log("companiesData Data Response", companiesData);
        this.allCompanies = companiesData;
        this.isLoading = false;
      })
      .catch((err) => {
        this.isError = true;
        this.isLoading = false;
      });
  }

  reset() {
    this.isLoading = false;
    this.isError = false;
    this.allCompanies = [];
  }
}
decorate(CompanyAndSiteStore, {
  allCompanies: observable,
  isLoading: observable,
  isError: observable,
  fetchAllCompanies:action,
  convertAndGetTableData:computed,
  getAllCompanies:computed,
  reset: action,
});

export default new CompanyAndSiteStore();
