import React, { Component } from 'react';
import SaveIcon from "@material-ui/icons/Save";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { Button } from '@material-ui/core';
import app_theme from '../../app_theme';
import { CheckListStore } from '../../stores';

const SaveSubmitButtonSection = ({containerStyle}) => {
    return ( 
        <div style={{ marginTop: 5, ...containerStyle }}>
        <Button
          variant="contained"
          color="primary"
          size="small"
          startIcon={<SaveIcon />}
          style={{ marginRight: 10,backgroundColor:app_theme.colors.success.main }}
          onClick={()=>CheckListStore.uploadCurrentCheckList()}
        >
          SAVE
        </Button>
        <Button
          variant="contained"
          color="default"
          size="small"
          startIcon={<CloudUploadIcon />}
          onClick={()=>CheckListStore.submitCurrentCheckList()}
        >
          SUBMIT
        </Button>
      </div>
     );
}
 
export default SaveSubmitButtonSection;
