import React, { Component } from "react";
import { Jumbotron, ProgressBar, Row, Col, Card } from "react-bootstrap";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import app_theme from "../../app_theme";

class CustomCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hover: false,
    };
  }

  render() {
    const { hover } = this.state;
    const { icon, sectionName, title, isComplete, containerStyle, ...props } = this.props;

    if(isComplete){
      return(
        <Card
        className="cursor-pointer"
        style={{
          backgroundColor:app_theme.colors.success.main,
          height:200,
          ...containerStyle,
        }}
        {...props}
      >
        <Card.Body>
        <div className="text-center font-weight-bold d-flex justify-content-center align-items-center p-5">
          <div>
            <small
              style={{ fontWeight: "bold", color: "white",textTransform:'uppercase',fontSize:16 }}
            >
              {sectionName}
            </small>
            <h5
              style={{
                fontWeight: "bold",
                color: "white",
                textTransform:'uppercase'
              }}
            >
              Complete
            </h5>
          </div>
        </div>
        </Card.Body>
   
        <div className="text-center p-2" style={{backgroundColor:app_theme.colors.success.light}}>
          <div>
           <EditOutlinedIcon/> EDIT
          </div>
        </div>
      </Card>
      )
    }
    return (
      <Card
        className="d-flex justify-content-center align-items-center cursor-pointer p-5"
        onMouseEnter={() => this.setState({ hover: true })}
        onMouseLeave={() => this.setState({ hover: false })}
        style={{
          backgroundColor: hover ? app_theme.colors.success.main : null,
          height:200,
          ...containerStyle,
        }}
        {...props}
      >
        <div className="text-center font-weight-bold">
          <div  style={{ fontSize: 40, marginBottom: 10, color: "white",display:'inline' }}>
          {hover ? (
            <ArrowForwardIosIcon
            />
          ) : (
            icon && <img 
            src={require(`../../assets/images/mainImages/${icon}`)}
            style={{width:60}}
            />
          )}
          </div>
        
          <div>
            <small
              style={{ fontWeight: "bold", color: hover ? "white" : "grey",textTransform:'uppercase' }}
            >
              {sectionName}
            </small>
            <h5
              style={{
                fontWeight: "bold",
                color: hover ? "white" : app_theme.colors.error.main,
                textTransform:'uppercase'
              }}
            >
              {title}
            </h5>
          </div>
        </div>
      </Card>
    );
  }
}

export default CustomCard;
