import React, { Component } from 'react';
import { Card, Button } from 'react-bootstrap';
import CustomButton from './CustomButton';
import app_theme from '../../app_theme';

const TryAgainBlock = ({containerStyle,onClickTryAgain,message}) => {
    return ( 
        <Card style={{color:'red',minHeight:200,display:'flex',justifyContent:'center',alignItems:'center',padding:5,...containerStyle}}>
         { message && <div style={{marginBottom:10}}>
           <p>{message}</p>
               </div>}
            <CustomButton backgroundColor={app_theme.colors.error.main} onClick={onClickTryAgain}>
                TRY AGAIN
            </CustomButton>
        </Card>
     );
}
 
export default TryAgainBlock;