import { AppWebRequest } from "../NetworkManager";
import qs from "qs";
import { UserStore, resetAllStore, initialStoreFetchFor } from "../../../stores";

class Auth {
  constructor() {}

  getAccessToken() {
    const user_info = localStorage.getItem("user_info")
      ? JSON.parse(localStorage.getItem("user_info"))
      : null;
    if (user_info && user_info.accessToken) {
      return user_info.accessToken;
    }
    return null;
  }

  getUserRole() {
    const user_info = JSON.parse(localStorage.getItem("user_info"));
    if (user_info) {
      return user_info.roles[0];
    }
    return null;
  }

  isAdmin() {
    const currentUser = this.getCurrentUser()
    if (currentUser?.roles?.includes("ROLE_ADMIN")) {
      return true;
    } else {
      return false;
    };
  }

  isMonitor() {
    const currentUser = this.getCurrentUser()
    if (currentUser?.roles?.includes("ROLE_MONITOR")) {
      return true;
    } else {
      return false;
    };
  }

  getCurrentUser() {
    const user_info = localStorage.getItem("user_info")
      ? JSON.parse(localStorage.getItem("user_info"))
      : null;
    if (user_info) {
      return user_info;
    }
    return null;
  }

  isAuthenticated() {
    if (this.getAccessToken()) {
      return true;
    } else {
      return false;
    }
  }

  login(payload) {
    var config = {
      data: qs.stringify(payload),
    };
    return AppWebRequest(`auth/signin`, "post", config).then((data) => {
      console.log("login data", data);
      localStorage.setItem("user_info", JSON.stringify(data));
      UserStore.setCurrentUserWithToken(data);
      if(this.isAdmin()){
        initialStoreFetchFor('ADMIN')
      }else if(this.isMonitor()){
        initialStoreFetchFor('MONITOR')
      }
      return data;
    });
  }

  fetchUserProfile() {
    return AppWebRequest("/auth/profile", "get");
  }

  fetchSystemUsers() {
    return AppWebRequest("/get-all-users", "get");
  }
      forgotPassword(email) {
          var config = {
              data: {email:email},
          }
          return AppWebRequest('auth/forgotPassword','post',config)
      }

  changePassword(payload) {
    var config = {
        data: payload,
    }
    return AppWebRequest('auth/changePassword','put',config)
}

  logOut() {
    localStorage.removeItem("user_info");
    resetAllStore()
  }
}

// export default observer(new Auth());
export default new Auth();
