import { AppWebRequest } from "../NetworkManager";

class CheckListHttpsCommunication {
  constructor() {}
  fetchAllCheckList() {
    return AppWebRequest("/get-all-CheckLists", "get");
  }

  fetchCheckListForSiteId(siteId) {
    return AppWebRequest(`/checkList/${siteId}`, "get");
  }

  uploadcheckListForSiteId(id,payload) {
    var config = {
      data: payload
    };
    return AppWebRequest("/checkList", "patch", config).then(
      (response) => {
        console.log("upload checklist response", response);
        return response;
      }
    );
  }

  updateCheckListForId(id,payload) {
    var config = {
      data: {
        _id:id,
        ...payload
      }
    };
    return AppWebRequest("/checkList", "patch", config).then(
      (response) => {
        console.log("update checklist response", response);
        return response;
      }
    );
  }

  checkListFileUpload(formData) {
    var config = {
                 data: formData,
                 headers:{
                    "Content-type": "multipart/form-data"
                 }
                    }
    return AppWebRequest('/checkList/image/upload','post',config)
    .then(response=>{
        console.log("file upload response",response);
        return response
    })
}

}

export default new CheckListHttpsCommunication();
