import React, { Component } from "react";
import UnderlineText from "../../../components/common/UnderlineText";
import { Form, Row, Col } from "react-bootstrap";
import { Container } from "@material-ui/core";
import CustomButton from "../../../components/common/CustomButton";
import { observer } from "mobx-react";
import { CompanyAndSiteStore, MonitorStore } from "../../../stores";
import TryAgainBlock from "../../../components/common/TryAgainBlock";
import { MySpinner, ErrorBlock } from "../../../components/common";
import queryString from "query-string";
import MonitorHttpsCommunication from "../../../services/https/ServerCommunication/MonitorHttpsCommunication";
import cogoToast from "cogo-toast";
import Ribbon from "../../../components/common/Ribbon";
import app_theme from "../../../app_theme";
import IOSSwitch from "../../../components/common/IOSSwitch";

class ScheduleMonitor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      errorData: null,
      coronaCheck: false,
      monitor_id: "",
      company_id: "",
      site_id: "",
    };
  }

  componentDidMount() {
    const qs = queryString.parse(this.props.location.search);
    if (qs.monitor_id) {
      this.setState({ monitor_id: qs.monitor_id });
    }
  }

  onClickScheduleNow = (e) => {
    e.preventDefault();
    const { monitor_id, company_id, site_id, coronaCheck } = this.state;
    this.setState({ isLoading: true, errorData: null });
    MonitorHttpsCommunication.assignSiteVisitSchedule({
      monitorId: monitor_id,
      companyId: company_id,
      siteId: site_id,
      isCoronaCheckRequired: coronaCheck,
    })
      .then((response) => {
        cogoToast.success(response.message || "Schduled Successfully");
        this.setState({
          monitor_id: "",
          company_id: "",
          site_id: "",
          visit_date: new Date(),
          visit_time: new Date(),
          isLoading: false,
        });
      })
      .catch((err) => {
        console.log("Error", err);
        cogoToast.error(err.message || "Error while scheduling");
        this.setState({ isLoading: false, errorData: err });
      });
  };

  render() {
    if (MonitorStore.isLoading || CompanyAndSiteStore.isLoading) {
      return <MySpinner />;
    }

    const {
      monitor_id,
      company_id,
      site_id,
      visit_date,
      visit_time,
      coronaCheck,
      isLoading,
      errorData,
    } = this.state;
    return (
      <div
        style={{ position: "relative", minHeight: window.innerHeight - 110 }}
      >
        {isLoading && <MySpinner containerStyle={{}} overlay />}
        <UnderlineText customClassName="text-uppercase">
          Schedule Monitor
        </UnderlineText>
        {MonitorStore.isError || CompanyAndSiteStore.isError ? (
          <TryAgainBlock
            onClickTryAgain={() => {
              MonitorStore.fetchAllMonitors();
              CompanyAndSiteStore.fetchAllCompanies();
            }}
          />
        ) : (
          <div style={{ marginTop: 40 }}>
            <Container maxWidth="sm">
              <Form onSubmit={this.onClickScheduleNow}>
                {errorData ? <ErrorBlock errorData={errorData} /> : null}
                <Row>
                  <Col>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                      <Form.Label>MONITORS</Form.Label>
                      <Form.Control
                        required
                        as="select"
                        className="classic"
                        value={monitor_id}
                        onChange={(e) =>
                          this.setState({ monitor_id: e.target.value })
                        }
                      >
                        <option value="">Please select</option>
                        {MonitorStore.getAllMonitors.map((monitor, index) => (
                          <option
                            key={index}
                            value={monitor._id}
                          >{`${monitor.firstName} ${monitor.lastName}`}</option>
                        ))}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                  <Col></Col>
                </Row>

                <Row>
                  <Col>
                    <Form.Group controlId="exampleForm.ControlSelect2">
                      <Form.Label>COMPANY</Form.Label>
                      <Form.Control
                        required
                        as="select"
                        className="classic"
                        value={company_id}
                        onChange={(e) =>
                          this.setState({ company_id: e.target.value })
                        }
                      >
                        <option value="">Please select</option>
                        {CompanyAndSiteStore.getAllCompanies.map(
                          (company, index) => (
                            <option
                              key={index}
                              value={company._id}
                            >{`${company.name}`}</option>
                          )
                        )}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="exampleForm.ControlSelect3">
                      <Form.Label>SITES</Form.Label>
                      <Form.Control
                        required
                        as="select"
                        className="classic"
                        value={site_id}
                        onChange={(e) =>
                          this.setState({ site_id: e.target.value })
                        }
                      >
                        <option value="">Please select</option>
                        {company_id &&
                          CompanyAndSiteStore.getAllCompanies
                            .find((comp) => comp._id == company_id)
                            .site_list.map((site, index) => (
                              <option
                                key={index}
                                value={site._id}
                              >{`${site.site_name}`}</option>
                            ))}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <Form.Group>
                      <IOSSwitch
                        checked={coronaCheck}
                        onChange={(e) =>
                          this.setState({ coronaCheck: e.target.checked })
                        }
                      />
                      <Form.Label style={{ marginLeft: 5 }}>
                        CORONA CHECK
                      </Form.Label>
                    </Form.Group>
                  </Col>
                </Row>

                <div className="mt-4 text-center">
                  <CustomButton withIcon square type="submit">
                    SCHEDULE NOW
                  </CustomButton>
                </div>
              </Form>
            </Container>
          </div>
        )}
        <Ribbon dividerStyle={{ position: "absolute", left: 0, top: "30%" }} />
        <Ribbon
          dividerStyle={{ position: "absolute", right: 0, bottom: "20%" }}
        />
      </div>
    );
  }
}

export default observer(ScheduleMonitor);
