import React, { Component } from "react";
import {
  BootstrapTable,
  TableHeaderColumn,
  InsertButton,
  SearchField,
  ExportCSVButton,
} from "react-bootstrap-table";
import { compareAsc, format } from 'date-fns'
import { Breadcrumb, Card, Dropdown } from "react-bootstrap";
import app_theme from "../../../app_theme";
import { Header, MySpinner } from "../../../components/common";
import UnderlineText from "../../../components/common/UnderlineText";
import queryString from 'query-string';
import { observer } from "mobx-react";
import { CompanyAndSiteStore } from "../../../stores";

class ViewCompany extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // company:this.props.location.company

    };
  }



  render() {
    if(CompanyAndSiteStore.isLoading){
      return <MySpinner />
    }
    const { history,location } = this.props;
    const companyId = queryString.parse(location.search).company_id
    var company = CompanyAndSiteStore.getAllCompanies.find(item=>item._id==companyId);
    
    const options = {
      sizePerPageDropDown: (props) => {
        return (
          <Dropdown>
            <Dropdown.Toggle variant="light" id="dropdown-basic">
              {props.currSizePerPage || 10}
            </Dropdown.Toggle>

            <Dropdown.Menu>
              {[10, 25, 50, 100].map((n, idx) => {
                return (
                  <Dropdown.Item
                    key={idx}
                    onClick={() => props.changeSizePerPage(n)}
                  >
                    {n}
                  </Dropdown.Item>
                );
              })}
            </Dropdown.Menu>
          </Dropdown>
        );
      },
    };

    return (
      <div>
        <Header
          containerStyle={{
            backgroundColor: "transparent",
            color: "white",
            marginBottom: 20,
          }}
          noCenter
          headerLeft={
            <UnderlineText underlineWidth="100%">
              {company.name} SITES
            </UnderlineText>
          }
          headerRight={null}
          rightContainerStyle={{ flex: 1 }}
        />

        <BootstrapTable
          version="4"
          data={company.site_list.map((site, index) => {
            site.sn = index + 1;
            return site;
          })}
          options={options}
          striped
          hover
          condensed
          pagination={true}
          search
          tableStyle={{ backgroundColor: "whitesmoke" }}
          tableHeaderClass="table-head-rb"
          trStyle={{ color: "black" }}
        >
          <TableHeaderColumn width="70" dataSort isKey dataField="sn">
            S.N
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="200"
            dataSort
            dataFormat={(cell, row) => "Construction Site"}
          >
            Type
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="200"
            dataSort
            dataField="site_name"
          >
            Site
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataSort
            dataField="company"
            dataFormat={(cell,row)=>company.name}
          >
            Company
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataSort
            dataField="postcode"
            dataFormat={(cell,row)=>row.address?.post_code}
          >
            Postcode
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataField="contactName"
          >
            Contact Name
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataField="contactNumber"
          >
            Contact Number
          </TableHeaderColumn>
          <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataField="contactEmail"
          >
            Contact Email
          </TableHeaderColumn>
          {/* <TableHeaderColumn
            tdStyle={{ whiteSpace: "normal" }}
            width="150"
            dataSort
            dataField="estimate"
            dataFormat={(cell,row)=>row.date_of_visit?format(new Date(row.date_of_visit), 'MM/dd/yyyy'):'Not Yet scheduled'}
          >
            Estimate
          </TableHeaderColumn> */}
        </BootstrapTable>
      </div>
    );
  }
}

export default observer(ViewCompany);
