import React, { Component } from "react";
import {
  Switch,
  Route,
  withRouter,
  Link,
  useRouteMatch,
  Redirect,
} from "react-router-dom";
import Login from "../pages/Auth/Login";
import Home from "../pages/DashBoard/Home";
import AppLayout from "../components/common/AppLayout";
import {
  CreateAccount,
  Monitors,
  Companies,
  ViewCompany,
  AssignMonitor,
  NewVisits,
  CheckList,
  CheckListSectionViewer,
  ToDo,
} from "../pages";
import PrivateRoute from "../components/common/PrivateRoute";
import RouteNotFound from "../pages/Error/RouteNotFound";
import Auth from "../services/https/Auth";
import { GlobalStore, UserStore,initialStoreFetchFor } from "../stores";
import { observer } from "mobx-react";
import ScheduleMonitor from "../pages/DashBoard/admin/ScheduleMonitor";
import Messages from "../pages/DashBoard/common/Messages";
import Help from "../pages/DashBoard/common/Help";
import { MySpinner } from "../components/common";
import ChangePassword from "../pages/DashBoard/common/ChangePassword";
import Welcome from "../pages/Auth/Welcome";

class Routes extends Component {
  constructor(props) {
    super(props);
    this.state={
      isLoading:true
    }
    this.authLoading();
  }
  
  UNSAFE_componentWillMount() {
    this.authLoading();
    this.initialFetch()
    GlobalStore.routeHistory = this.props.history;
  }

  authLoading() {
    // this.setState({isLoading:true})
    // UserStore.setCurrentUserWithToken(Auth.getCurrentUser());
  
    if(Auth.isAuthenticated()){
      UserStore.setCurrentUserWithToken(Auth.getCurrentUser());
      this.setState({isLoading:false})
      this.props.history.push('/dashboard')
    }else{
      this.setState({isLoading:false})
      this.props.history.push('/auth/welcome')
    }
  }

  initialFetch(){
    if(Auth.isAuthenticated()){
      if(Auth.isAdmin()){
        initialStoreFetchFor('ADMIN')
      }else{
        initialStoreFetchFor('MONITOR')
      }
    }
  }

  render() {
    const { location } = this.props;
    if(this.state.isLoading){
      return(
        <AppLayout noLayout={true}>
          <div style={{backgroundColor:'black'}}> 
          <MySpinner title="Authenticating..."/>
          </div>
        </AppLayout>
      )
    }
    return (
      <AppLayout
        noLayout={location.pathname == "/auth/login" || location.pathname == "/auth/welcome"}
        history={this.props.history}
      >
        <div>
          <Switch>
            <Redirect from="/" to="/auth/welcome" exact />
            <Route
              path="/auth/welcome"
              render={(props) =>
                UserStore.isLoggedIn ? (
                  <Redirect to="/dashboard" />
                ) : (
                  <Welcome {...props} />
                )
              }
              exact
            />
            <Route
              path="/auth/login"
              render={(props) =>
                UserStore.isLoggedIn ? (
                  <Redirect to="/dashboard" />
                ) : (
                  <Login {...props} />
                )
              }
              exact
            />
            {!UserStore.isAdmin && (
              <Redirect from="/dashboard" to="/dashboard/new-visit" exact />
            )}
              <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard"
              component={Home}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/monitors/create-account"
              component={CreateAccount}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/monitors"
              component={Monitors}
              exact
            />
               <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/monitors/add-schedule"
              component={ScheduleMonitor}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/companies"
              component={Companies}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/companies/company-viewer"
              component={ViewCompany}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/companies/company-viewer/assign-monitor"
              component={AssignMonitor}
              exact
            />
            {/* Monitors Routes */}
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/new-visit"
              component={NewVisits}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/todo"
              component={ToDo}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/todo/checklist"
              component={CheckList}
              exact
            />
            <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/todo/checklist/checklist-section-viewer"
              component={CheckListSectionViewer}
              exact
            />
              <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/messages"
              component={Messages}
              exact
            />
              <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/help"
              component={Help}
              exact
            />
               <PrivateRoute
              isAuthenticated={UserStore.isLoggedIn}
              path="/dashboard/change-password"
              component={ChangePassword}
              exact
            />

            {/* Route Not Found  */}
            <Redirect to="/" exact />
          </Switch>
        </div>
      </AppLayout>
    );
  }
}

export default withRouter(observer(Routes));
