import React, { Component, useRef } from "react";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CameraEnhanceOutlinedIcon from "@material-ui/icons/CameraEnhanceOutlined";
import { Card, Button, Form } from "react-bootstrap";
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import { Button as MUIButton } from "@material-ui/core";
import app_theme from "../../app_theme";
import { Header, MySpinner } from "../common";
import SquareWithText from "../common/SquareWithText";
import MUICustomToolTip from "../common/MUICustomToolTip";
import CustomButton from "../common/CustomButton";
import PhotoCamera from "@material-ui/icons/PhotoCamera";
import { useState } from "react";
import CheckListHttpsCommunication from "../../services/https/ServerCommunication/CheckListHttpsCommunication";
import cogoToast from "cogo-toast";
import httpUrl from "../../services/https/httpUrl";

class ScoreBoard extends Component {
  state = {
    open: false,
  };
  static RowItem = ({
    item,
    section,
    label,
    onClickScore,
    onClickAddComment,
    onClickTakePhoto,
    containerStyle,
    score,
    open,
  }) => {
    const [fileUploadLoading, setFileUploadLoading] = useState(false);
    const commentRef = useRef();

    const imageSelectHandler = (e) => {
      const image_as_file = e.target.files[0];
      const formData = new FormData();
      formData.append("image", image_as_file);
      setFileUploadLoading(true);
      CheckListHttpsCommunication.checkListFileUpload(formData)
        .then((response) => {
          onClickTakePhoto(response.path);
          setFileUploadLoading(false);
        })
        .catch((err) => {
          setFileUploadLoading(false);
          cogoToast.error(err.message || "Error File Uploading");
        });
    };
    return (
      <Header
        containerStyle={{
          paddingLeft: 25,
          paddingRight: 20,
          marginBottom: 5,
          backgroundColor: app_theme.colors.background.grey,
          flexWrap: "wrap",
          ...containerStyle,
        }}
        headerLeft={<small>{label}</small>}
        headerCenter={
          section.name?.toUpperCase() == "CORONAVIRUS" || section.name?.toUpperCase() == "CORONAVIRUS " ? (
            <div style={{ display: "flex" }}>
              <SquareWithText
                number={<small>YES</small>}
                borderColor={app_theme.colors.success.main}
                textColor={
                  score == "YES"
                    ? app_theme.colors.success.contrastText
                    : app_theme.colors.success.main
                }
                backgroundColor={
                  score == "YES" ? app_theme.colors.success.main : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore('YES')}
              />
              <SquareWithText
                number={<small>NO</small>}
                borderColor={app_theme.colors.warning.main}
                textColor={
                  score == "NO"
                    ? app_theme.colors.warning.contrastText
                    : app_theme.colors.warning.main
                }
                backgroundColor={
                  score == "NO" ? app_theme.colors.warning.main : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore("NO")}
              />
              <SquareWithText
                number={<small>N/A</small>}
                borderColor={app_theme.colors.black}
                textColor={
                  score == "N/A"
                    ? app_theme.colors.white
                    : app_theme.colors.black
                }
                backgroundColor={
                  score == "N/A" ? app_theme.colors.black : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore("N/A")}
              />
            </div>
          ) : (
            <div style={{ display: "flex" }}>
              <SquareWithText
                number={1}
                borderColor={app_theme.colors.error.main}
                textColor={
                  score == "1"
                    ? app_theme.colors.error.contrastText
                    : app_theme.colors.error.main
                }
                backgroundColor={
                  score == "1" ? app_theme.colors.error.main : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore(1)}
              />
              <SquareWithText
                number={2}
                borderColor={app_theme.colors.warning.main}
                textColor={
                  score == "2"
                    ? app_theme.colors.warning.contrastText
                    : app_theme.colors.warning.main
                }
                backgroundColor={
                  score == "2" ? app_theme.colors.warning.main : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore(2)}
              />
              <SquareWithText
                number={3}
                borderColor={app_theme.colors.warning.light}
                textColor={
                  score == "3"
                    ? app_theme.colors.warning.contrastText
                    : app_theme.colors.warning.light
                }
                backgroundColor={
                  score == "3" ? app_theme.colors.warning.light : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore(3)}
              />
              <SquareWithText
                number={4}
                borderColor={app_theme.colors.success.main}
                textColor={
                  score == "4"
                    ? app_theme.colors.success.contrastText
                    : app_theme.colors.success.main
                }
                backgroundColor={
                  score == "4" ? app_theme.colors.success.main : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore(4)}
              />
              <SquareWithText
                number={<small>N/A</small>}
                borderColor={app_theme.colors.black}
                textColor={
                  score == "N/A"
                    ? app_theme.colors.white
                    : app_theme.colors.black
                }
                backgroundColor={
                  score == "N/A" ? app_theme.colors.black : "transparent"
                }
                containerStyle={{
                  marginRight: 20,
                }}
                onClick={() => onClickScore("N/A")}
              />
            </div>
          )
        }
        headerRight={
          <div style={{ display: "flex" }}>
            <MUICustomToolTip
              headerComponent={
                <Button
                  size="sm"
                  style={{
                    padding: 3,
                    marginRight: 5,
                    border: "1px solid black",
                    backgroundColor: "white",
                    color: "black",
                  }}
                >
                  <div className="d-flex flex-column justify-content-center align-items-center">
                    <EditOutlinedIcon
                      style={{
                        padding: 0,
                        display: "inline-block",
                        textAlign: "center",
                        fontSize: 16,
                      }}
                    />
                    <span
                      style={{
                        fontSize: 8,
                        padding: 0,
                        display: "inline-block",
                      }}
                    >
                      ADD COMMENT
                    </span>
                  </div>
                </Button>
              }
            >
              <Form.Group controlId="exampleForm.addComment">
                <Form.Label>Comment</Form.Label>
                <Form.Control
                  defaultValue={item.comment}
                  ref={commentRef}
                  as="textarea"
                  rows="3"
                />
              </Form.Group>
              <CustomButton
                square
                onClick={() => onClickAddComment(commentRef.current.value)}
              >
                Save
              </CustomButton>
            </MUICustomToolTip>

            <MUICustomToolTip
              headerComponent={
                <Button
                  size="sm"
                  style={{
                    padding: 3,
                    marginRight: 5,
                    border: "1px solid black",
                    backgroundColor: "white",
                    color: "black",
                  }}
                >
                  <div className="d-flex flex-column justify-content-center align-items-center">
                    <CameraEnhanceOutlinedIcon
                      style={{
                        padding: 0,
                        display: "inline-block",
                        textAlign: "center",
                        fontSize: 16,
                      }}
                    />
                    <span
                      style={{
                        fontSize: 8,
                        padding: 0,
                        display: "inline-block",
                      }}
                    >
                      ADD PHOTO
                    </span>
                  </div>
                </Button>
              }
            >
              {fileUploadLoading ? (
                <MySpinner
                  title="Uploading file..."
                  containerStyle={{ height: 100 }}
                />
              ) : (
                <div>
                  <center>
                    <div
                      className="d-flex justify-content-around"
                      style={{
                        border: `1px solid ${app_theme.colors.success.main}`,
                        borderRadius: "2px",
                        paddingTop: "4px",
                        borderRadius: 10,
                      }}
                    >
                      <input
                        accept="image/*"
                        style={{ display: "none" }}
                        id="contained-button-file"
                        multiple
                        type="file"
                        onChange={imageSelectHandler}
                      />
                      <label htmlFor="contained-button-file">
                        <MUIButton
                          variant="contained"
                          color="primary"
                          component="span"
                          style={{
                            backgroundColor: app_theme.colors.success.main,
                          }}
                        >
                          Upload
                        </MUIButton>
                      </label>
                      <input
                        accept="image/*;capture=camera"
                        style={{ display: "none" }}
                        id="icon-button-file"
                        type="file"
                        onChange={imageSelectHandler}
                      />
                      <label htmlFor="icon-button-file">
                        <MUIButton
                          variant="contained"
                          color="primary"
                          component="div"
                          style={{
                            backgroundColor: app_theme.colors.success.main,
                            display: "inline-block",
                            marginRight: 5,
                          }}
                        >
                          Camera
                        </MUIButton>
                      </label>
                    </div>
                  </center>
                  <div
                    style={{
                      marginTop: 5,
                      border: `1px solid ${app_theme.colors.success.main}`,
                      padding: 5,
                      minHeight: 50,
                      borderRadius: 10,
                    }}
                  >
                    <div style={{ marginBottom: 5 }}>Images</div>
                    <div style={{ display: "flex", flexWrap: "wrap" }}>
                      {item.photoPath.map((imgPath, index) => {
                        return (
                          <div className="mr-2 mt-1" key={index}>
                            <div
                              style={{
                                height: 100,
                                width: 100,
                              }}
                            >
                              <img
                                src={`${httpUrl.BASE_URL}${imgPath}`}
                                alt="Image"
                                style={{ maxWidth: "100%", height: "auto" }}
                              />
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              )}
            </MUICustomToolTip>

            <MUICustomToolTip
              headerComponent={
                <Button
                  size="sm"
                  style={{
                    padding: 3,
                    border: "1px solid black",
                    backgroundColor: "white",
                    color: "black",
                  }}
                >
                  <div className="d-flex flex-column justify-content-center align-items-center">
                    <InsertDriveFileIcon
                      style={{
                        padding: 0,
                        display: "inline-block",
                        textAlign: "center",
                        fontSize: 16,
                      }}
                    />
                    <span
                      style={{
                        fontSize: 8,
                        padding: 0,
                        display: "inline-block",
                      }}
                    >
                      ADD DOCUMENT
                    </span>
                  </div>
                </Button>
              }
            >
              {fileUploadLoading ? (
                <MySpinner
                  title="Uploading file..."
                  containerStyle={{ height: 100 }}
                />
              ) : (
                <div>
                  <center>
                    <div
                      className="d-flex justify-content-around"
                      style={{
                        border: `1px solid ${app_theme.colors.success.main}`,
                        borderRadius: "2px",
                        paddingTop: "4px",
                        borderRadius: 10,
                      }}
                    >
                      <input
                        accept="image/*"
                        style={{ display: "none" }}
                        id="contained-button-file"
                        multiple
                        type="file"
                        onChange={imageSelectHandler}
                      />
                      <label htmlFor="contained-button-file">
                        <MUIButton
                          variant="contained"
                          color="primary"
                          component="span"
                          style={{
                            backgroundColor: app_theme.colors.success.main,
                          }}
                        >
                          Upload
                        </MUIButton>
                      </label>
                      <input
                        accept="image/*;capture=camera"
                        style={{ display: "none" }}
                        id="icon-button-file"
                        type="file"
                        onChange={imageSelectHandler}
                      />
                      <label htmlFor="icon-button-file">
                        <MUIButton
                          variant="contained"
                          color="primary"
                          component="div"
                          style={{
                            backgroundColor: app_theme.colors.success.main,
                            display: "inline-block",
                            marginRight: 5,
                          }}
                        >
                          Camera
                        </MUIButton>
                      </label>
                    </div>
                  </center>
                  <div
                    style={{
                      marginTop: 5,
                      border: `1px solid ${app_theme.colors.success.main}`,
                      padding: 5,
                      minHeight: 50,
                      borderRadius: 10,
                    }}
                  >
                    <div style={{ marginBottom: 5 }}>Images</div>
                    <div style={{ display: "flex", flexWrap: "wrap" }}>
                      {item.photoPath.map((imgPath, index) => {
                        return (
                          <div className="mr-2 mt-1" key={index}>
                            <div
                              style={{
                                height: 100,
                                width: 100,
                              }}
                            >
                              <img
                                src={`${httpUrl.BASE_URL}${imgPath}`}
                                alt="Image"
                                style={{ maxWidth: "100%", height: "auto" }}
                              />
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              )}
            </MUICustomToolTip>
          </div>
        }
        leftContainerStyle={{
          flex: "none",
          width: "20%",
          marginRight: 15,
        }}
        centerContainerStyle={{
          justifyContent: "flex-start",
          flex: 1,
        }}
        rightContainerStyle={{}}
      />
    );
  };
  render() {
    const {
      listCount,
      question,
      children,
      containerStyle,
      rowContainerStyle,
      open,
    } = this.props;
    return (
      <Card style={{ color: "black", paddingBottom: 5, ...containerStyle }}>
        <div
          style={{
            padding: 5,
            backgroundColor: app_theme.colors.background.grey,
            display: "flex",
            alignItems: "center",
          }}
        >
          <div>
            <span
              style={{
                fontSize: 18,
                color: app_theme.colors.success.main,
                marginRight: 5,
                fontWeight: "bold",
              }}
            >
              {listCount}
            </span>
            {question}
          </div>
        </div>
        <div
          style={{
            paddingLeft: 10,
            paddingRight: 10,
            paddingTop: 5,
            ...rowContainerStyle,
          }}
          className="disable-scrollbars"
        >
          {children?.length > 1
            ? children.map((child, index) => {
                return React.cloneElement(child, {
                  containerStyle: {
                    backgroundColor:
                      index % 2 == 1
                        ? app_theme.colors.background.grey
                        : "none",
                  },
                });
              })
            : children}
        </div>
      </Card>
    );
  }
}

export default ScoreBoard;

ScoreBoard.defaultProps = {
  listCount: 1.1,
  question:
    "Does the external appearance of the site present a positive image of the industry?",
};
