import React, { Component } from "react";
import { useNetwork } from "react-use";
import app_theme from "../../app_theme";

const OfflineNotice = () => {
  const networkInfo = useNetwork();
  if (networkInfo.online) {
    return null;
  }
  return (
    <div
      style={{
        padding: 2,
        width: "100%",
        position: "fixed",
        backgroundColor: app_theme.colors.error.main,
        color: app_theme.colors.error.contrastText,
        zIndex: 1000000000,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div style={{ fontSize: 10, fontWeight: "bold" }}>
        No Internate Connection
      </div>
    </div>
  );
};

export default OfflineNotice;
