import React from "react";
import app_theme from "../../app_theme";
const styles = {
  containerStyle: {
    display: "flex",
    minHeight: 70,
    backgroundColor: 'white',
    color:'black'
  },
  scoreContainerStyle: {
    display: 'flex',
    width:70,
    fontSize: 27,
    color:'white',
    fontWeight: 700,
    backgroundColor:'green',
    justifyContent:'center',
    alignItems: 'center'
  },
  textContainerStyle: {
    display: 'flex',
    flex:1,
    paddingLeft: 15,
    backgroundColor: app_theme.colors.custom.grey[300],
    alignItems: 'center'
  }
};
const SectionResultItem = ({ score, text,containerStyle,scoreContainerStyle,textContainerStyle ,...props}) => {
  return (
    <div style={{...styles.containerStyle,...containerStyle}} {...props} className="rounded">
      <div style={{...styles.scoreContainerStyle,...scoreContainerStyle}} className="rounded-left">
          <div>{score}</div>
      </div>

      <div style={{...styles.textContainerStyle,...textContainerStyle}} className="rounded-right">
            <strong>{text}</strong>
      </div>
    </div>
  );
};

export default SectionResultItem;

SectionResultItem.defaultProps={
    score:2,
    text:'test text please replace with props'
}

