import React, { Component } from "react";
import app_theme from "../../app_theme";
import { Card, Typography, Container } from "@material-ui/core";
import { Form, InputGroup, FormControl, Button } from "react-bootstrap";
import cogoToast from 'cogo-toast';
import PersonIcon from "@material-ui/icons/Person";
import VpnKeyIcon from "@material-ui/icons/VpnKey";
import { Link } from "react-router-dom";
import '../../css/login.style.css';
import ForgotPasswordPopup from './ForgotPasswordPopup';
import Footer from '../../components/common/Footer';
import { ValidateEmail } from '../../utils/HelperMethods';
import CustomButton from "../../components/common/CustomButton";
import Auth from '../../services/https/Auth';
import { MySpinner, ErrorBlock } from '../../components/common';
import Ribbon from "../../components/common/Ribbon";
import OfflineNotice from "../../components/common/OfflineNotice";

class Login extends Component {
  state = { 
    username: '',
    password : '',
    showModal : false,
    isLoading:false,
    errorData:null
   }

      // Login function
      Login = (e) =>{
          e.preventDefault()
          const{username,password} = this.state;
          this.setState({isLoading:true, errorData : null})
          var payload = {
            username : username,
            password : password
          };
          Auth.login(payload)
          .then(response=>{
            // cogoToast.success("Login Success")
          })
          .catch(err=>{
            console.log("err",err)
              this.setState({isLoading:false,errorData:err})
              cogoToast.error(err.message||"Error Occured!!!")
          })
      }

  render() { 
    const{ username, password, showModal, isLoading, errorData } = this.state;
    return ( 
      <div
      style={{
        backgroundColor: app_theme.colors.primary.main,
        color: app_theme.colors.primary.contrastText,
      }}
    >
       {isLoading && <MySpinner containerStyle={{}} overlay/>}
      <table style={{ width: "100%", height: "100vh" }}>
        <tr
          style={{
            textAlign: "center",
            verticalAlign: "middle",
            height: "100%",
          }}
        >
          <th style={{}}>
          <Container maxWidth="md">
  <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  width: "100%",
                }}
              >
                <form className="login-form" onSubmit={this.Login}>
                  <Card style={{
                     backgroundColor: app_theme.colors.primary.main,
                     color:app_theme.colors.primary.contrastText,
                     padding:10
                  }}>

                 
                     {errorData && <ErrorBlock errorData={errorData} />}
                        <div style={{ marginTop: 20 }}>
                          <Form.Group className="mb-2 text-left">
                          <Form.Label><small>EMAIL/USERNAME *</small></Form.Label>
                            <FormControl
                              type="text"
                              required
                              autoFocus
                              value={username}
                              onChange={(e) =>
                                this.setState({
                                  username: e.target.value,
                                })
                              }
                            />
                          </Form.Group>
                          <div>

                          <Form.Group className="mb-2 text-left">
                          <Form.Label><small>PASSWORD *</small></Form.Label>
                            <FormControl
                            type="password"
                            required
                            value={password}
                            onChange={(e) =>
                              this.setState({ password: e.target.value.trim() })
                            }
                            />
                          </Form.Group>
                          <small
                              style={{ textAlign: "right", cursor: "pointer",color:app_theme.colors.text.grey }}
                              onClick={() =>
                                this.setState({ showModal: true })
                              }
                              className="d-block"
                            >
                              Forgot password?
                            </small>
                          </div>

                          <CustomButton
                            square
                            withIcon
                            type="submit"
                            width="100%"
                            buttonStyle={{marginTop:20}}
                          >
                            LOGIN
                          </CustomButton>
                        </div>
                        </Card>
                </form>
              </div>
</Container>
           
          </th>
        </tr>
        <Footer/>
      </table>

        {showModal ? 
        <ForgotPasswordPopup show={showModal} 
        onHide={() => this.setState({
            showModal : false
        })}/>
        : null}

<Ribbon dividerStyle={{position:'absolute',left:0,top:'30%'}}/>
        <Ribbon dividerStyle={{position:'absolute',right:0,bottom:'20%'}}/>
    </div>
     );
  }
}
 
export default Login;