import React from "react";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme, Card } from "@material-ui/core";
import { Button } from 'react-bootstrap';
import { observer } from 'mobx-react';
import app_theme from "../../app_theme";
import SideBarMenu from "./AdminSideBar";
import { useLocation } from "react-router-dom";
import ProfileMenu from './ProfileMenu';
import Footer from './Footer';
import AdminSideBar from './AdminSideBar';
import MonitorSideBar from './MonitorSideBar';
import CancelIcon from '@material-ui/icons/Cancel';
import { GlobalStore, UserStore } from '../../stores';
import MySpinner from './MySpinner';
import ErrorBlock from './ErrorBlock';
import Auth from "../../services/https/Auth";
import OfflineNotice from "./OfflineNotice";

const drawerWidth = 300;

const useStyles = makeStyles((theme) => ({
  root: {
    // display: "flex",
    minHeight: "100vh",
    backgroundColor : app_theme.colors.primary.dark,
    backgroundRepeat:"repeat-y",
     backgroundSize:"100%", 
      overflow:"auto",
    color: app_theme.colors.primary.contrastText
  },
  title: {
    flexGrow: 1,
    display: "flex",
    alignItems:'center'
  },
  drawer: {
    [theme.breakpoints.up("md")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    backgroundColor: app_theme.colors.primary.main,
    zIndex:100,
    color:app_theme.colors.text.primary,
    [theme.breakpoints.up("md")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
    "&:focus": {
      outline: "none",
    },
  },
  // necessary for content to be below app bar
  toolbar: {
    // display: 'flex',
    // alignItems: 'center',
    // justifyContent: 'flex-end',
    // padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor:app_theme.colors.primary.main,
    color:app_theme.colors.text.primary
  },
  content: {
    display:'block',
    [theme.breakpoints.up("md")]: {
      marginLeft:drawerWidth,
    },
    flexGrow: 1,
    padding: theme.spacing(2),
  },
}));

function AppLayout(props) {
  const { window, children, noLayout,noLayoutWrapperClassName, history } = props;
  const classes = useStyles();
  const location = useLocation();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const container =
    window !== undefined ? () => window().document.body : undefined;
  return noLayout ? (
    <div style={{minHeight:'100vh'}}>{props.children}</div>
  ) : (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>

          <h6 variant="h6" noWrap className={classes.title}>
          <img src={require('../../assets/images/CCS_logo_white.png')} style={{width:70}} alt="CCS Logo" />
          </h6>

          <ProfileMenu history={history}/>
        </Toolbar>
      </AppBar>

      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        {/* For Mobile/Tab sidebar */}
        <Hidden mdUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            <div className={classes.toolbar}>
            <CancelIcon style={{fontSize:30,margin:20}} onClick={handleDrawerToggle}/>
            </div>
              <Divider style={{ backgroundColor: app_theme.colors.primary.dark }} />
            {UserStore.isAdmin ? <AdminSideBar history={history} clicked={handleDrawerToggle}/> :<MonitorSideBar history={history} clicked={handleDrawerToggle}/> }
          </Drawer>
        </Hidden>

        {/* For Desktop sidebar */}
        <Hidden smDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
              <div className={classes.toolbar} />
              <Divider style={{ backgroundColor: app_theme.colors.primary.dark }} />
            {UserStore.isAdmin ? <AdminSideBar history={history} clicked={()=>{}}/> :<MonitorSideBar history={history} clicked={()=>{}}/> }
          </Drawer>
        </Hidden>
      </nav>

      <main className={classes.content}>
        <div className={classes.toolbar} />
        <div style={{ minHeight : '80%' }}>
        {children}
        </div>
        <Footer/>
      </main>
    </div>
  );
}

AppLayout.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default observer(AppLayout);
