import  GlobalStore from './GlobalStore';
import  UserStore from './UserStore';
import  CheckListStore from './CheckListStore';
import  MonitorStore from './MonitorStore';
import  CompanyAndSiteStore from './CompanyAndSiteStore';

 export {
    GlobalStore,
    UserStore,
    CheckListStore,
    MonitorStore,
    CompanyAndSiteStore
 }

export function initialStoreFetchFor(role){
    if(role=="ADMIN"){
        MonitorStore.fetchAllMonitors()
        CompanyAndSiteStore.fetchAllCompanies()
    }else if(role=="MONITOR"){
        MonitorStore.fetchAllAssignedSitesForCurrentMonitor()
    }
}

export function resetAllStore(){
    MonitorStore.reset()
    UserStore.reset()
    CheckListStore.reset()
    MonitorStore.reset()
    CompanyAndSiteStore.reset()
}