import React, { Component } from 'react';
import { Alert } from 'react-bootstrap';

export default class ErrorBlock extends Component {
  constructor(props) {
    super(props);
    this.state = {
        show:true
    };
  }

  render() {
      const{errorData} = this.props
    return (
        <Alert show = {this.state.show} dismissible variant="danger" onClose = {()=>this.setState({show:false})}>
        <Alert.Heading style = {{textAlign:'center'}}>{errorData.message?errorData.message:"Oh snap! You got an error!"}</Alert.Heading>
            {errorData.data?errorData.data.map((item,key)=><p key = {key}>{item.msg}</p>):null}
        </Alert>
    );
  }
}
